<template>
  <!--  -->
  <el-dialog
    class="custom-dialog"
    width="520px"
    lock-scroll
    append-to-body
    :close-on-click-modal="false"
    title=""
    :model-value="visible"
    @close="handleClose"
  >
    <!--    -->
    <div class="not-container container" v-if="certificationStatus === 0">
      <p class="word"></p>
      <p class="prompt">,</p>
      <el-button
        type="primary"
        color="#0056ff"
        @click="goPage('/certificationService')"
        >
      </el-button>
    </div>
    <!--     -->
    <div class="waiting-container container" v-if="certificationStatus === 1">
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-certification-waiting"></use>
      </svg>
      <p class="word"></p>
      <p class="prompt">
        1~3
      </p>
    </div>
    <!--    -->
    <div class="pass-container container" v-if="certificationStatus === 2">
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-certification-pass"></use>
      </svg>
      <p class="word"></p>
      <p class="prompt">
        
      </p>
      <el-button type="primary" color="#0056ff" @click="buyProduct"
        >
      </el-button>
    </div>
    <!--    -->
    <div class="refuse-container container" v-if="certificationStatus === 3">
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-certification-refuse"></use>
      </svg>
      <p class="word"></p>
      <p class="prompt">{{ checkAdvice }}</p>
      <el-button
        type="primary"
        color="#0056ff"
        @click="goPage('/certificationService?checkStatus=0')"
        >
      </el-button>
    </div>
  </el-dialog>
</template>

<script lang="ts" setup>
import { useVueRouter } from "@assets/useHook";
import { updateMsgReadStateApi } from "@api/common-api";
defineProps<{
  visible: boolean;
  checkAdvice?: string;
  certificationStatus: number | string;
}>();
const emits = defineEmits<{
  (e: "update:visible", value: boolean): void;
}>();
const { goPage } = useVueRouter();
const handleClose = () => {
  updateMsgReadState().then(() => {
    emits("update:visible", false);
  });
};
const buyProduct = () => {
  updateMsgReadState().then(() => {
    goPage("/marketService");
    emits("update:visible", false);
  });
};
const updateMsgReadState = () => {
  return updateMsgReadStateApi.getData({ state: 1 });
};
</script>

<style lang="scss" scoped>
.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 440px;
  min-height: 170px;
  margin: 0 auto;
  svg {
    width: 36px;
    height: 36px;
  }
  .word {
    font-size: 18px;
    color: #242934;
    line-height: 20px;
    margin: 12px 0 20px;
  }
  .prompt {
    font-size: 14px;
    color: #9ca6b9;
    line-height: 1;
    text-align: center;
  }
  .el-button {
    margin-top: 40px;
  }
}
</style>
