// 
export const getCashOutListForPageApi = {
  getData(params) {
    return http.post(`/agent-system-rest/getCashOutListForPage`, params);
  },
  handleData(data) {
    return {
      list: (data?.records || []).map((item) => ({
        ...item,
        amount: toMoney(item.amount),
        accountBalance: toMoney(item.accountBalance),
      })),
      total: data?.total || 0,
    };
  },
};

// 
export const pageForCashCouponApi = {
  getData(params) {
    return http.post(`/agent-system-rest/pageForCashCoupon`, params);
  },
  handleData(data) {
    return {
      list: (data?.records || []).map((item) => ({
        ...item,
        statusText: statusTextMap[item.status],
        couponAmount: toMoney(item.couponAmount),
        useAmount: toMoney(item.useAmount),
      })),
      total: data?.total || 0,
    };
  },
};

