export const cashOutListRecordsApi = {
  getData(params) {
    return http.post("/agent-system-rest/cashOut/listRecords", params);
  },
  handleData(data) {
    return {
      list: (data?.records || []).map((item) => ({
        ...item,
        createdTime: dayjs(item.createdTime).format("YYYY-MM-DD HH:mm:ss"),
        checkStatusText: checkStatusMap[item.checkStatus],
      })),
      total: data?.total || 0,
    };
  },
};

